<template id = "car-big-data-feedback-template">
  <div id="feedback" style="display: flex; flex-direction: column">
    <el-row style="flex: none; display: flex; height: 60px">
      <div
        style="
          display: flex;
          align-items: center;
          margin-left: 10px;
          flex: none;
        "
      >
        <el-button stytle="color = #719cdc" @click="isCollapse = !isCollapse">
          <i class="el-icon-s-fold"></i>
        </el-button>
      </div>
      <el-menu
        class="el-menu-demo"
        mode="horizontal"
        router
        default-active="/feedback"
        @select="handleSelect"
        background-color="transparent"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="flex: auto; display: flex"
      >
        <el-menu-item
          index="/home"
          style="margin-left: 70px; margin-right: 10px"
          ></el-menu-item
        >
        <el-menu-item index="/feedback"></el-menu-item>
        <el-menu-item index="/engineering" style="margin-left: auto"
          ></el-menu-item
        >
        <el-menu-item index="/sale" style="margin-right: 40px"></el-menu-item>
      </el-menu>
      <div
        style="
          margin-left: auto;
          display: flex;
          align-items: center;
          margin-right: 10px;
          flex: none;
        "
      >
        <el-dropdown>
          <i
            class="el-icon-setting"
            style="margin-right: 10px; color: #ffffff"
          ></i>
          <el-dropdown-menu slot="dropdown">
            <el-button @click="changeData"></el-button>
            <el-button @click="destoryUser"></el-button>
          </el-dropdown-menu>
        </el-dropdown>

        <span style="color: white">  </span>
      </div>
    </el-row>
    <el-container style="border: 1px solid #eee; display: flex; flex: auto">
      <!-- <el-aside width="200px" style="background-color: #024195"> -->
      <el-menu
        style="flex: none"
        :default-openeds="['1', '2']"
        :collapse="isCollapse"
        :collapse-transition="false"
        background-color="rgba(35,81,162,0.5)"
        text-color="#fff"
        active-text-color="white"
      >
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-message"></i>
            <span slot="title"></span>
          </template>
          <el-menu-item-group>
            <template slot="title"></template>

            <!---->
            <el-select v-model="mvalue" placeholder="">
              <el-option
                v-for="item in carModels.carModelList"
                :key="item.mvalue"
                :label="item.model"
                :value="item.mvalue"
              >
              </el-option>
            </el-select>
          </el-menu-item-group>
          <el-menu-item-group>
            <template slot="title"></template>

            <!---->
            <el-select v-model="value" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span slot="title"></span>
          </template>
          <el-menu-item-group>
            <router-link to="/feedback/feedbackTarget" tag="el-menu-item"
              ></router-link
            >
            <router-link to="/feedback/feedbackComment" tag="el-menu-item"
              ></router-link
            >
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
      <!-- </el-aside> -->
      <el-container style="flex: auto">
        <router-view></router-view>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "Feedback",

  mounted() {
    axios
      .get("../../../static/data/CarModel.json")
      .then((response) => {
        this.carModels=response.data;

      });
  },

  data() {
    return {
      //
      isCollapse: false,

      activeIndex: "1",

      //
      input: "",

      //
      options: [
        {
          value: "1",
          label: "1",
        },
        {
          value: "2",
          label: "15",
        },
        {
          value: "3",
          label: "510",
        },
        {
          value: "4",
          label: "1020",
        },
        {
          value: "5",
          label: "2050",
        },
        {
          value: "6",
          label: "50100",
        },
        {
          value: "7",
          label: "100",
        },
      ],
      //
      carModels:[

      ],
      value: "",
      mvalue: "",
    };
  },

  methods: {
    //
    destoryUser() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    //
    changeData() {
      this.$prompt("6~20+", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        //6~20
        inputPattern: /^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]{6,20})$/,
        inputErrorMessage: "",
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>

<style>
.el-header {
  background-color: transparent;
  color: #ffffff;
  line-height: 60px;
}

html,
body {
  margin: 0;
  height: 100%;
  width: 100%;
}

.el-aside {
  color: #333;
}

#feedback {
  background: url("./resource/b01.png") no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: top;
}

.el-menu.el-menu--horizontal {
  border-bottom: none;
}
</style>



